.vote-container {
  padding: 2.083vw 0 0;

  .hide-lg {
    display: none;
  }

  .purple-right-arrow {
    background: url(../../images/v2/vote/purple-right-arrow-new.svg) no-repeat center 0.2rem;
    content: '';
    width: 14px;
    height: 1rem;
    display: inline-block;
    margin-left: 2px;
  }

  .purple-right-arrow2 {
    background: url(../../images/v2/vote/purple-right-arrow2.svg) no-repeat center 0.4em;
    content: '';
    width: 5px;
    height: 1rem;
    display: inline-block;
    margin-left: 6px;
    transform: translateY(1px);
  }

  .purple-right-arrow3 {
    background: url(../../images/v2/vote/purple-right-arrow3.svg) no-repeat center 0.4em;
    content: '';
    width: 5px;
    height: 1rem;
    display: inline-block;
    margin-left: 6px;
    transform: translateY(1px);
  }

  .common-flex {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 16px;

    .common-title {
      // font-size: 1.111vw;
      font-size: 16px;
      font-weight: 600;
      color: #fff;
      display: flex;
      align-items: center;
      line-height: 22px;

      &::before {
        content: '';
        width: 4px;
        height: 1em;
        display: inline-block;
        background: #4d54ff;
        border-radius: 1px;
        margin-right: 10px;
      }
    }

    .common-desc {
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: rgba(255, 255, 255, 0.8);
      padding-left: 14px;
    }
  }

  .step-container {
    display: flex;
    gap: 20px;
    margin: 20px 0 20px;

    .left-steps {
      display: flex;
      // flex: 2 1 auto;
      flex: 0 0 66%;

      padding: 17px 30px;
      background: #31333a;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      display: flex;
      flex-direction: row;
      gap: 10px;

      .step {
        display: flex;
        gap: 10px;
        align-items: center;

        &:nth-child(3) {
          width: 240px;
        }
      }

      .step-number {
        border-radius: 50%;
        background: rgba(255, 255, 255, 0.04);
        // background: red;
        height: 30px;
        width: 30px;
        // text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #9195fb;
        flex-shrink: 0;
        font-weight: 700;
      }

      .step-text-box {
        display: flex;
        flex-direction: column;

        .step-title {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          color: #ffffff;
        }

        .step-desc {
          font-style: normal;
          font-weight: 500;
          font-size: 12px;
          line-height: 19px;
          color: rgba(255, 255, 255, 0.6);
          margin-top: 2px;
        }
      }

      .step-arrow {
        background: url(../../images/v2/vote/step-arrow.svg) no-repeat center;
        width: 10px;
        height: 5px;
      }
    }

    .right-forum {
      padding: 17px 30px;
      flex: auto;
      background: #31333a url(../../images/v2/vote/right-forum-card.png) no-repeat center right 20px;
      background-size: 111px 43px;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 10px;

      .forum-title {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #ffffff;
        margin-bottom: 4px;
        margin-top: 6px;
      }

      .forum-desc {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #9195fb;

        // &:hover {
        //   border-bottom: 1px solid #9195fb;
        //   padding-bottom: 1px;
        // }
      }
    }
  }

  .body-container {
    display: flex;
    gap: 20px;
  }

  .proposal-container {
    flex: 0 0 66%;
    background: #31333a;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding-bottom: 16px;

    .proposal-header {
      padding: 26px 30px 0px;
      gap: 10px;

      display: flex;
      justify-content: space-between;
      align-items: center;

      .proposal-left {
        display: flex;
        gap: 8px;

        .proposal-title {
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 28px;
          color: #ffffff;
        }

        .proposal-count {
          background: rgba(76, 84, 255, 0.3);
          border-radius: 1px;
          font-weight: 600;
          font-size: 12px;
          height: 25px;
          line-height: 16px;
          text-transform: capitalize;

          color: #ffffff;
          align-self: center;
          padding: 5px;
        }
      }

      .proposal-right {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        line-height: 22px;
        padding: 6px 16px;
        background: #292b32;
        border-radius: 5px;

        .new-proposal {
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          line-height: 20px;
          color: #ffffff;
          display: flex;
          align-items: center;

          .bell {
            background: url(../../images/v2/vote/white-bell.svg) no-repeat center;
            width: 14px;
            height: 14px;
            flex-shrink: 0;
            margin-right: 6px;
          }
        }

        .new-proposal-txid {
          padding-left: 4px;
          font-weight: 500;
          font-size: 12px;
          line-height: 20px;
          color: #9195fb;
          box-sizing: border-box;

          &:hover {
            box-sizing: border-box;
            //  border-bottom: 1px solid #9195FB;
            // padding-bottom: 1px;
            text-decoration: underline;
          }
        }
      }
    }

    .active-proposal {
      background: #292b32;
      border-radius: 10px;
      padding: 40px 20px;
      margin: 0px 30px;
      position: relative;

      .ticket-icon {
        background: url(../../images/v2/vote/vote-ticket-icon.svg) no-repeat center;
        width: 18px;
        height: 20px;
        margin-right: auto;
        flex-shrink: 0 !important;

        &:hover {
          background-image: url(../../images/v2/vote-detail/vote.svg);
        }
      }

      .corner-tag {
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(143, 247, 226, 0.16);
        font-weight: 500;
        font-size: 12px;
        line-height: 17px;
        text-transform: capitalize;
        color: rgba(143, 247, 226, 0.8);
        padding: 2px 12px;
        border-radius: 10px 0;
      }

      .p-body {
        display: flex;
        // padding: 42px 20px;
        justify-content: space-between;
        background: transparent;
        border-radius: 2px;
        flex-direction: column;
        gap: 20px;
      }

      .p-first-row {
        display: flex;
        gap: 10px;
        justify-content: space-between;
        align-items: center;
        overflow-wrap: anywhere;

        .p-title {
          font-weight: 600;
          font-size: 20px;
          line-height: 28px;
          color: #9195fb;
        }

        .vote-btn {
          flex-shrink: 0;
          background: #4c54ff;
          border-radius: 2px;
          padding: 6px 26px;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          text-align: center;
          color: #ffffff;
          height: fit-content;
          transition: all 0.3s ease-in-out;

          &:hover {
            background: #9195fb;
          }
        }
      }

      .p-second-row {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .p-desc {
          display: flex;
          align-items: center;
          width: fit-content;
          flex-wrap: wrap;
          gap: 10px;
        }

        .p-id {
          background: rgba(255, 255, 255, 0.08);
          border-radius: 2px;
          font-style: normal;
          font-weight: 500;
          font-size: 12px;
          height: 24px;
          line-height: 18px; // not 16px to center it

          color: #ffffff;
          padding: 4px 10px;
          flex-shrink: 0;
        }

        .p-countdown-text {
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          color: rgba(255, 255, 255, 0.6);
          flex-shrink: 0;
        }

        .p-countdown-date {
          font-weight: 500;
          font-size: 14px;
          line-height: 19px;

          color: #ffffff;
          flex-shrink: 0;
        }

        .p-vote-bar {
          display: flex;
          align-items: center;
          width: 272px;
          justify-content: flex-end;

          &.hide-lg {
            gap: 4px;
          }

          .p-for {
            font-style: normal;
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            text-align: right;
            // min-width: 44px;
            color: #8ff7e2;
          }

          .p-for-bar {
            background: #8ff7e2;
            mix-blend-mode: normal;
            transform: matrix(-1, 0, 0, 1, 0, 0);
            width: 20px;
            height: 6px;
            margin-left: 6px;
            border-radius: 0 1px 1px 0;

            &::after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              border-bottom: 6px solid #22232b;
              border-right: 6px solid transparent;
              width: 0;
            }

            &.per100 {
              margin-right: 6px;
              width: 100% !important;
              border-radius: 1px;

              &::after {
                display: none;
              }
            }
          }

          .p-against-bar {
            background: #ff5266;
            mix-blend-mode: normal;
            transform: matrix(1, 0, 0, -1, 0, 0);
            width: 20px;
            height: 6px;
            margin-right: 6px;
            border-radius: 0 1px 1px 0;

            &::before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              border-bottom: 6px solid #22232b;
              border-right: 6px solid transparent;
              width: 0;
            }

            &.per100 {
              margin-left: 6px;
              width: 100% !important;
              border-radius: 1px;

              &::before {
                display: none;
              }
            }
          }

          .p-default-bar {
            height: 6px;
            margin: 0 6px;
            background: rgba(255, 255, 255, 0.1);
          }

          .p-against {
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            text-align: right;
            // min-width: 44px;
            color: #ff5266;
          }
        }
      }
    }

    .no-new-proposal {
      background: #292b32 url(../../images/v2/vote/dark-no-new-proposal.png) no-repeat center right 40px;
      background-size: 323px 79px;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      gap: 16px;
      padding: 40px 30px;
      margin: 0px 30px;

      .proposal-title {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #ffffff;
      }

      .proposal-desc {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #9195fb;
        width: fit-content;

        // &:hover {
        //   border-bottom: 1px solid #9195fb;
        //   padding-bottom: 1px;
        // }
      }
    }

    .past-proposal-list {
      display: flex;
      flex-direction: column;

      .j-nodata {
        padding-top: 70px;

        .nodata-text {
          opacity: 0.8;
        }
      }

      .p-item {
        padding: 25px 30px;
        gap: 20px;
        display: flex;
        flex-direction: column;
        transition: all 0.3s ease-in-out;

        &:hover {
          background: #393b42;
        }

        &:hover > .first-row .arrow-right {
          opacity: 1;
          margin-left: 14px;
        }

        .first-row {
          display: flex;
          align-items: center;
          justify-content: space-between;
          gap: 40px;

          .p-title {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: #ffffff;
          }

          .ticket-icon {
            background: url(../../images/v2/vote-detail/vote.svg) no-repeat center;
            width: 14px;
            height: 15px;
            margin-left: 10px;
            margin-top: 3px;
            vertical-align: top;
            display: inline-block;
            background-size: contain;

            &:hover {
              background-image: url(../../images/v2/vote/vote-ticket-icon.svg);
            }
          }

          .arrow-right {
            background: url(../../images/v2/arrow-right.svg) no-repeat center center;
            width: 12px;
            height: 10px;
            background-size: 100%;
            display: inline-block;
            opacity: 0;
            transition: 0.3s;
            top: -1px;
          }

          .p-green-tick {
            flex-shrink: 0;
            margin-right: 35px;
            background: url(../../images/v2/vote/green-tick.svg) no-repeat center center;
            height: 20px;
            width: 20px;
          }

          .p-red-exclamation {
            flex-shrink: 0;
            margin-right: 35px;
            background: url(../../images/v2/vote/dark-red-exclamation.svg) no-repeat center center;
            height: 20px;
            width: 20px;
            background-size: 20px;
          }
        }

        .second-row {
          display: flex;
          align-items: center;
          justify-content: space-between;

          .p-left {
            display: flex;
            align-items: center;
            gap: 10px;

            .p-id {
              font-weight: 500;
              font-size: 12px;
              line-height: 12px;

              color: rgba(255, 255, 255, 0.6);
              background: rgba(255, 255, 255, 0.08);
              border-radius: 2px;
              padding: 5px 10px 4px;
            }

            .p-date {
              font-weight: 500;
              font-size: 14px;
              line-height: 19px;

              color: rgba(255, 255, 255, 0.6);
            }
          }

          .p-status {
            width: 92px;
            font-weight: 500;
            font-size: 12px;
            line-height: 17px;
            text-transform: capitalize;

            color: #ffffff;
            background: rgba(255, 255, 255, 0.08);
            border-radius: 2px;
            padding: 4px;
            text-align: center;

            // &.en {
            // width: 110px;
            // }
          }
        }
      }
    }

    .load-more {
      cursor: pointer;
      width: fit-content;
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      margin: auto;
      color: rgba(255, 255, 255, 0.6);
      display: flex;
      justify-content: center;

      &:hover {
        color: #fff;

        &::after {
          opacity: 1;
        }
      }

      &::after {
        content: '';
        display: flex;
        background: url(../../images/v2/arrow-white.svg) no-repeat center center;
        height: 1rem;
        width: 1rem;
        opacity: 0.6;
      }
    }
  }

  .right-panel {
    flex: auto;
    display: flex;
    flex-direction: column;
    gap: 20px;

    .my-vote-container {
      // flex: auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      // height: 100px;
      background: #31333a;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 10px;

      .my-vote-header {
        padding: 26px 30px 0px;
        align-self: start;
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
        text-transform: capitalize;
        color: #ffffff;
      }

      .unconnected-wallet-logo {
        display: flex;
        background: url(../../images/v2/vote/unconnected-wallet.svg) no-repeat center center;
        height: 80px;
        width: 80px;
      }

      .my-vote-text {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;

        color: #ffffff;
        padding: 18px 0 40px;
      }

      .tooltip-icon {
        display: flex;
        // background: url(../../images/v2/vote/white-qustion-mark-tooltip.svg) no-repeat center center;
        height: 14px;
        width: 14px;
        background-size: 14px 14px;
      }

      .hide-lg {
        display: none !important;
      }

      .total-votes {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        /* identical to box height, or 143% */

        text-align: center;

        color: rgba(255, 255, 255, 0.6);
        display: flex;
        align-items: center;
        gap: 6px;

        .j-tooltip-icon {
          min-width: 14px;
          width: 14px;
          height: 14px;
          background-size: 14px;
          transform: translateY(-1px);
        }

        .active {
          color: #9195fb !important;

          &.white {
            color: #4d54ff !important;
          }
        }
      }

      .total-votes-amount {
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 27px;
        text-transform: capitalize;

        color: #ffffff;
        margin-top: 8px;
      }

      .vote-info-box {
        background: #292b32;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        width: calc(100% - 60px);
        padding: 20px 15px;
        gap: 16px;
        margin-top: 30px;
        margin-bottom: 30px;

        .vote-info-row {
          display: flex;
          justify-content: space-between;

          .vote-info-text {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            color: rgba(255, 255, 255, 0.6);
          }

          .vote-info-amount {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            text-align: right;
            color: #ffffff;
            max-width: 235px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;

            &.clickable {
              color: #9195fb;
              cursor: pointer;
            }
          }
        }
      }

      .my-vote-btn-wrapper2 {
        padding: 0 30px 26px;
        width: 100%;
        display: flex;
        gap: 21px;
      }

      .my-vote-btn-wrapper {
        padding: 0 30px 26px;
        width: 100%;
      }

      .my-vote-btn {
        cursor: pointer;
        background: #4c54ff;
        border-radius: 2px;

        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        color: #ffffff;
        padding: 10px;
        width: 100%;
        transition: all 0.3s ease-in-out;

        &:hover {
          background: #9195fb;
        }
      }

      .my-vote-btn2 {
        cursor: pointer;
        // background: rgba(255, 255, 255, 0.04);
        border-radius: 2px;
        color: #9195fb;
        border: solid 1px #9195fb;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        padding: 10px;
        width: 100%;
        transition: all 0.3s ease-in-out;

        &:hover {
          opacity: 0.7;
        }
      }

      .tick-vote-logo {
        background: url(../../images/v2/vote/tick-vote.png) no-repeat center center;
        background-size: 100%;
        height: 80px;
        width: 80px;
      }

      .invalid-vote {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        text-align: center;

        color: #9195fb;
        margin-bottom: 26px;
        cursor: pointer;
      }
    }

    .faq-container {
      display: flex;
      flex-direction: column;
      background: #31333a;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 10px;

      .faq-header {
        padding: 20px 30px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .faq-title {
          font-weight: 600;
          font-size: 16px;
          line-height: 22px;
          text-transform: capitalize;

          color: #ffffff;
        }

        .faq-more {
          font-weight: 500;
          font-size: 12px;
          line-height: 16px;
          text-align: right;
          display: flex;
          color: rgba(255, 255, 255, 1);
          opacity: 0.6;

          &::after {
            content: '';
            background: url(../../images/v2/vote/white-right-arrow.svg) no-repeat center center;
            height: 1em;
            width: 1em;
            margin-top: 1px;
          }

          &:hover {
            opacity: 1;
          }
        }
      }

      .faq-body {
        padding: 0 30px 20px;

        ul {
          display: flex;
          flex-direction: column;
          gap: 16px;
          margin: 0 !important;
        }

        li {
          &::before {
            content: '\2022';
            color: #ffffff;
            font-weight: bold;
            display: inline-block;
            width: 1em;
            margin-left: -1em;
          }
        }

        a {
          font-weight: 400;
          font-size: 14px;
          line-height: 20px;
          color: rgba(255, 255, 255, 0.6);

          &:hover {
            font-weight: 500;
            color: #ffffff;
          }
        }
      }
    }
  }

  .ant-divider-inner-text {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-transform: capitalize;

    color: rgba(255, 255, 255, 0.6);
    padding: 10px;
  }

  .ant-divider {
    margin: 20px 0;
    border-top-color: #ffffff;
    opacity: 0.06;

    &.proposal-title {
      // width: calc(100% - 60px);
      // margin-left: 30px;
      width: 100%;
      margin-bottom: 30px;
      margin-top: 26px;
      min-width: auto;
    }

    &.my-vote-title {
      margin-left: 0;
      margin-bottom: 40px;
    }
  }

  .ant-divider-with-text {
    opacity: 1;

    &::before,
    &::after {
      opacity: 0.06;
    }
  }

  .mobile-menu {
    display: none;
    justify-content: space-around;

    padding-bottom: 10px;
    padding-top: 20px;

    border-bottom: 1px solid rgba(255, 255, 255, 0.06);

    .menu-item {
      position: relative;
      font-weight: 500;
      font-size: 16px;
      line-height: 22px;
      text-transform: capitalize;
      cursor: pointer;
      color: rgba(255, 255, 255, 0.4);

      .p-left {
        display: flex;
        gap: 4px;
      }

      .p-title {
        font-size: 16px;
        line-height: 22px;
        text-transform: capitalize;
        // color: #FFFFFF;
      }

      .p-count {
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 16px;

        color: #9195fb;

        background: rgba(76, 84, 255, 0.3);
        border-radius: 1px;
        padding: 2px 3px 1px;
        display: flex;
        align-items: center;
      }

      &::after {
        content: '';
        display: block;
        max-width: 36px;
        height: 2px;
        position: absolute;
        bottom: -12px;
        left: 50%;
        transform: translateX(-50%);
        background: #4c54ff;
        border-radius: 1px;
        // transition: width 0.3s ease-in-out;
      }

      &:hover,
      &.current {
        color: #ffffff;
        font-weight: 600;

        .p-count {
          color: #ffffff;
          background: #4c54ff;
        }

        &::after {
          width: 100%;
          min-width: 40px;
        }
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

