                    flex-direction: row;
                  }
                }

                .vote-btn {
                  flex-shrink: 0;
                  background: #4c54ff;
                  border-radius: 2px;
                  padding: 3px 20px;
                  color: #ffffff;
                  font-weight: 400;
                  font-size: 12px;
                  line-height: 20px;
                  transition: all 0.3s ease-in-out;

                  &:hover {
                    background: #9195fb;
                  }
                }

                display: flex;
                width: 100%;
                justify-content: space-between;
              }
            }
          }
        }

        .past-proposal-list {
          .p-item {
            padding: 25px 15px;

            .first-row {
              .p-title {
                font-size: 14px;
                line-height: 20px;
              }

              .p-red-exclamation,
              .p-green-tick {
                height: 16px;
                width: 16px;
                background-size: 16px !important;
                margin-right: 0px;
              }
            }

            .second-row {
              .p-date {
                font-size: 12px;
                line-height: 16px;
              }

              .p-status {
                font-weight: 400;
              }
            }
          }
        }
      }

      .right-panel {
        .my-vote-container {
          .hide-lg {
            display: flex !important;
          }

          .hide-md {
            display: none;
          }

          .space-60 {
            height: 50px;
          }

          .unconnected-wallet-logo,
          .tick-vote-logo {
            height: 70px;
            width: 70px;
            background-size: 70px;
            margin: 0 !important;
          }

          .my-vote-text {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            padding-bottom: 50px;
          }

          .ant-divider {
            display: none;
          }

          .my-vote-header {
            display: none;
          }

          .total-votes-amount {
            font-size: 16px;
            line-height: 22px;
          }

          .vote-info-box {
            width: calc(100% - 30px);
          }

          .my-vote-btn-wrapper,
          .my-vote-btn-wrapper2 {
            padding: 0 15px 30px;
            gap: 23px;
          }
        }

        .faq-container {
          .faq-header {
            padding: 20px 15px 0;
          }

          .ant-divider-horizontal {
            margin: 20px 0;
          }

          .faq-body {
            padding: 0 15px 20px;
          }
        }
      }
    }

    .ant-divider {
      margin: 30px 0;
    }
  }

  .ant-modal {
    width: calc(100% - 40px);
  }

  .ant-modal-content {
    // max-width: calc(100% - 20px);
    margin: auto;

    .ant-divider-horizontal {
      margin-bottom: 20px !important;
    }
  }

  .vote-modal > .ant-modal-content {
    .ant-modal-body {
      min-height: 488px;

      .tick-vote-logo {
        margin-bottom: 10px;
        margin-top: 0;
      }

      .v-desc2 {
        padding-left: 15px;
        padding-right: 15px;
        padding-top: 20px;
      }

      .vote-input,
      .exceed-remain-votes,
      .modal-btn {
        width: calc(100% - 30px);
      }
    }
  }

  .ant-divider-with-text {
    padding: 0px 15px !important;
  }
}

@media screen and (max-width: 1400px) {
  .vote-container .step-container .left-steps .step {
    &:nth-child(3) {
      width: auto;
    }
  }
}

@media screen and (max-width: 1100px) {
  .j-container.j-home .body-container .proposal-container .active-proposal .p-body .p-second-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 26px;
  }
}

@media screen and (min-width: 1700px) {
  .vote-container .step-container .left-steps .step {
    &:nth-child(3) {
      width: auto;
    }
  }
}

@media screen and (max-width: 433px), (min-width: 799px) and (max-width: 1073px) {
  .new-proposal-txid {
    padding-left: 20px !important;
  }
}
