.m-tabsbar {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 68px;
  align-items: center;
  justify-content: center;
  background: #31333a;
  padding-top: 4px;
  z-index: 1000;

  .menu {
    display: flex;
    flex-grow: 1;
    overflow: hidden;
    height: 48px;
  }

  .menu-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    text-decoration: none;
    &.current {
      .home {
        background: url(../../images/v2/mobile/tabsbar/icon_home_selected.svg) no-repeat center/cover;
      }
      .strx {
        background: url(../../images/v2/mobile/tabsbar/icon_strx_selected.svg) no-repeat center/cover;
      }

      .energy {
        background: url(../../images/v2/mobile/tabsbar/icon_energy_selected.svg) no-repeat center/cover;
      }

      .dao {
        background: url(../../images/v2/mobile/tabsbar/icon_dao_selected.svg) no-repeat center/cover;
      }
    }
  }

  .menu-icons {
    display: block;
    width: 26px;
    height: 26px;
    background: url(../../images/v2/mobile/tabsbar/icon_home.svg) no-repeat center/cover;

    &.strx {
      background: url(../../images/v2/mobile/tabsbar/icon_strx.svg) no-repeat center/cover;
    }

    &.energy {
      background: url(../../images/v2/mobile/tabsbar/icon_energy.svg) no-repeat center/cover;
    }

    &.dao {
      background: url(../../images/v2/mobile/tabsbar/icon_dao.svg) no-repeat center/cover;
    }
  }

  .menu-item-label {
    display: block;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    transform: scale(0.833);
    color: #fff;
    line-height: 14px;
  }

  &.white {
    background: #fff;

    .menu-item {
      &.current {
        .home {
          background: url(../../images/v2/mobile/tabsbar/white-theme/icon_home_selected.svg) no-repeat center/cover;
        }
        .strx {
          background: url(../../images/v2/mobile/tabsbar/white-theme/icon_strx_selected.svg) no-repeat center/cover;
        }

        .energy {
          background: url(../../images/v2/mobile/tabsbar/white-theme/icon_energy_selected.svg) no-repeat center/cover;
        }

        .dao {
          background: url(../../images/v2/mobile/tabsbar/white-theme/icon_dao_selected.svg) no-repeat center/cover;
        }
      }
    }

    .menu-icons {
      display: block;
      width: 26px;
      height: 26px;
      background: url(../../images/v2/mobile/tabsbar/white-theme/icon_home.svg) no-repeat center/cover;

      &.strx {
        background: url(../../images/v2/mobile/tabsbar/white-theme/icon_strx.svg) no-repeat center/cover;
      }

      &.energy {
        background: url(../../images/v2/mobile/tabsbar/white-theme/icon_energy.svg) no-repeat center/cover;
      }

      &.dao {
        background: url(../../images/v2/mobile/tabsbar/white-theme/icon_dao.svg) no-repeat center/cover;
      }
    }

    .menu-item-label {
      color: #22232b;
    }
  }
}
